<template>
        <publicMWrap :title="t(TOP_TRADER.title)">
                <p class="subTitle">{{ t(TOP_TRADER.desc) }}</p>
                <div class="traderSwiper">
                        <div class="swiperContainer">
                                <div class="inner" :style="{ transform: `translateX(${translateX}px)` }">
                                        <div class="traderBox" :class="{ active: index === currentIndex }"
                                                v-for="(item, index) in trList" :key="index"
                                                :style="{ marginRight: spaceBetween + 'px' }" @click="moveTo(index)">
                                                <div class="container">
                                                        <img class="avatar" alt="" :src="item.avatar" />
                                                        <img class="dot" alt="" src="@/assets/images/dot.png" />
                                                        <p class="name">{{ t(item.firstName) }} {{ t(item.lastName) }}
                                                        </p>
                                                        <p class="tag">{{ t(TOP_TRADER.trader) }}</p>
                                                        <div class="descWrap">
                                                                <div class="top">
                                                                        <div class="nation">
                                                                                {{ t(item.from) }}
                                                                        </div>
                                                                        <div class="speech">{{ t(item.shortSpeech) }}
                                                                        </div>
                                                                        <div class="speech">{{ item.speech }}</div>
                                                                </div>
                                                                <div class="bottom">
                                                                        <div class="price">{{ item.profits }}</div>
                                                                        <div class="profits">
                                                                                {{ t(TOP_TRADER.profitsStr) }}
                                                                        </div>
                                                                </div>
                                                        </div>
                                                </div>
                                                <p class="outsideName">{{ t(item.lastName) }}<br>{{ t(item.firstName) }}
                                                </p>
                                                <img class="outsideMore" alt="" src="@/assets/images/icons/more.png" />
                                        </div>
                                </div>
                        </div>
                        <i class="iconfont icon-arrows tsBtn prev" v-if="currentIndex !== 0" @click="bindPrev"></i>
                        <i class="iconfont icon-arrows tsBtn next" v-if="currentIndex !== TOP_TRADER.list.length - 1"
                                @click="bindNext"></i>
                </div>
        </publicMWrap>
</template>
<script setup>
import { useCheckMobile } from '@/store/isMobile'
import { TOP_TRADER } from "@/data/challengesData"
const { t } = useI18n()
const spaceBetween = ref(0)
const currentIndex = ref(2)
const boxWidth = ref(0)
const translateX = ref(0)
const isMobile = computed(() => useCheckMobile().getItem())
const n = 4
const trList = ref([])

const setTransferList = ()=>{
        const copyList = deepClone(TOP_TRADER.list)
        if(isMobile.value){
                let arr = copyList.slice(2, 4)
                let rest = copyList.slice(0, 2).concat(copyList.slice(4))
                trList.value = arr.concat(rest)
                currentIndex.value = 0
                translateX.value = 0
        }else {
                trList.value = copyList
                currentIndex.value = 2
        }
}

const moveTo = _debounce((index) => {
        if (currentIndex.value === index) return
        if (!isMobile.value) {
                const dir = currentIndex.value < index ? "next" : "prev"
                if (dir === "next") {
                        if (index >= n - 1) {
                                translateX.value = -boxWidth.value * (index - (n - 1))
                        }
                } else {
                        if (index <= 0) {
                                translateX.value = 0
                        } else if (index === TOP_TRADER.list.length - 2 && !isMobile.value) {
                                translateX.value = -boxWidth.value * (index - 2)
                        } else {
                                translateX.value = -boxWidth.value * (index - 1)
                        }
                }
        }
        currentIndex.value = index
}, 500, { 'leading': true, 'trailing': false })

const bindPrev = () => {
        if (currentIndex.value === 0) return
        moveTo(currentIndex.value - 1)
}
const bindNext = () => {
        if (currentIndex.value === TOP_TRADER.list.length - 1) return
        moveTo(currentIndex.value + 1)
}
const setSB = () => {
        const windowWidth = isMobile.value ? 375 : 1920
        const val = 12
        nextTick(() => {
                spaceBetween.value = (window.innerWidth / windowWidth) * val
                const traderBox = document.getElementsByClassName("traderBox")
                for(let i=0;i<traderBox.length;i++){
                        if(!traderBox[i].className.includes("active")){
                                boxWidth.value = traderBox[i].offsetWidth + spaceBetween.value
                                break;
                        }
                }
                setResizeData()
        })
};
const setResizeData = ()=>{
        const i = currentIndex.value
        if (i <= n-1) {
                translateX.value = 0
        } else {
                translateX.value = -boxWidth.value * (i - n + 1)
        }
        setTransferList()
}
onMounted(() => {
        setSB()
        setTransferList()
        window.addEventListener("resize", _debounce(setSB, 500));
});
onUnmounted(() => {
        window.removeEventListener("resize", _debounce(setSB, 500));
});
</script>
<style scoped lang="scss" src="./index.scss"></style>
